const elementOpen = document.querySelectorAll('[data-link] > details[open]');
const elementClose = document.querySelectorAll('[data-link] > details');

function accordion() {

  function onToggle(event) {
    if (!elementOpen) {
      // eslint-disable-next-line no-alert
      alert('–º–µ–Ω—è –Ω–µ—Ç –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü–µ');
    } else {
      if (event.target.open) {
        elementOpen.forEach((el) => {
          // –ò—Å–∫–ª—é—á–∞–µ–º –∏–∑ –ø–µ—Ä–µ–±–æ—Ä–∞ –µ–ª–µ–º–µ–Ω—Ç –∫–æ—Ç–æ—Ä—ã–π –º—ã —Ç–æ–ª—å–∫–æ —á—Ç–æ –æ—Ç–∫—Ä—ã–ª–∏
          if (el === event.target) {
            return;
          }

          // –ó–∞–∫—Ä—ã–≤–∞–µ–º –≤—Å–µ –æ—Å—Ç–∞–ª—å–Ω—ã–µ –µ–ª–µ–º–µ–Ω—Ç—ã <details>
          el.open = false;
        });
      }
    }
  }

  // –í–µ—à–∞–µ–º –Ω–∞–±–ª—é–¥–∞—Ç–µ–ª—å –Ω–∞ –≤—Å–µ –µ–ª–µ–º–µ–Ω—Ç—ã <details> –≤–Ω—É—Ç—Ä–∏ –∞–∫–æ—Ä–¥–µ–æ–Ω–∞
  if (!elementClose) {
    // eslint-disable-next-line no-alert
    alert('–º–µ–Ω—è –Ω–µ—Ç –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü–µ');
  } else {
    elementClose.forEach((el) => el.addEventListener('toggle', onToggle));
  }
}

export {accordion};
